import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  getMovies = async () => {
    //함수형 component, async: 비동기 -> 기다려야한다.
    // const movies = await axios.get("https://yts-proxy.now.sh/list_movies.json"); //axios로 데이터 불러오기
    // console.log(movies.data.data.movies) 이걸 줄인게 아래,, es6씨벌ㅠㅜㅠ
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    ); //axios로 데이터 불러오기
    // this.setState({ movies: movies }); //state의 movies를 axios로 받은 movies로 바꿔주는 것
    this.setState({ movies, isLoading: false }); //state의 movies를 axios로 받은 movies 로 바꿔주는것 simple
  }; //await axios : axios를 기다려라! async써야 await사용 가능

  componentDidMount() {
    this.getMovies();
  } //axios.get은 느리니 이 didmount가 오래걸릴 수도 있다고 javascipt에게 말해줘야함

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading..</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
